"use client";

import { useEffect, useState } from "react";

export default function Finale() {
  const [totalScore, setTotalScore] = useState(0);
  const [fakeButtonClicked, setFakeButtonClicked] = useState(false);
  const [challengeSolved, setChallengeSolved] = useState(false);
  const [answer, setAnswer] = useState("");
  const [feedback, setFeedback] = useState("");

  // Load total score from localStorage
  useEffect(() => {
    if (typeof window !== "undefined") {
      const savedScore = Number(localStorage.getItem("totalScore")) || 0;
      setTotalScore(savedScore);
    }
  }, []);

  // Handle fake button click
  const handleFakeButtonClick = () => {
    setFakeButtonClicked(true);
  };

  // Update answer input
  const handleAnswerChange = (e) => {
    setAnswer(e.target.value);
  };

  // Check riddle answer
  const checkAnswer = () => {
    const correctAnswer = "fire";
    if (answer.trim().toLowerCase() === correctAnswer) {
      setChallengeSolved(true);
      setFeedback("");
    } else {
      setFeedback("Incorrect, try again!");
    }
  };

  // Styles
  const containerStyle = {
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    justifyContent: "center",
    minHeight: "100vh",
    padding: "2rem",
    background: "linear-gradient(135deg, #0a192f 0%, #1e3a8a 100%)",
    color: "#e2e8f0",
    fontFamily: "'Inter', sans-serif",
    position: "relative",
    overflow: "hidden",
    backgroundImage:
      "radial-gradient(circle at 50% 50%, rgba(56, 189, 248, 0.1) 0%, transparent 50%)",
  };

  const h1Style = {
    fontSize: "clamp(2.5rem, 6vw, 4rem)",
    color: "#38bdf8",
    fontWeight: 700,
    textShadow:
      "0 0 10px rgba(56, 189, 248, 0.5), 0 0 20px rgba(56, 189, 248, 0.3)",
    marginBottom: "1.5rem",
    letterSpacing: "0.05em",
  };

  const pStyle = {
    fontSize: "clamp(1rem, 2.5vw, 1.25rem)",
    color: "#93c5fd",
    marginBottom: "1.5rem",
    maxWidth: "600px",
    lineHeight: "1.6",
    fontWeight: 300,
    textShadow: "0 0 5px rgba(147, 197, 253, 0.3)",
  };

  const buttonStyle = {
    padding: "clamp(0.5rem, 1.5vw, 0.75rem) clamp(1rem, 2.5vw, 1.5rem)",
    fontSize: "clamp(0.875rem, 2vw, 1rem)",
    background: "linear-gradient(45deg, #2563eb, #60a5fa)",
    color: "#fff",
    border: "none",
    borderRadius: "10px",
    cursor: "pointer",
    boxShadow: "0 4px 15px rgba(0, 0, 0, 0.3)",
    transition: "transform 0.3s ease, box-shadow 0.3s ease",
    fontWeight: 500,
  };

  const buttonHoverStyle = {
    transform: "scale(1.05)",
    boxShadow: "0 6px 20px rgba(0, 0, 0, 0.4)",
  };

  const inputStyle = {
    padding: "0.75rem",
    fontSize: "1rem",
    border: "1px solid rgba(56, 189, 248, 0.5)",
    borderRadius: "8px",
    background: "rgba(15, 23, 42, 0.8)",
    color: "#e2e8f0",
    width: "min(100%, 300px)",
    marginBottom: "1rem",
    boxShadow: "0 2px 8px rgba(0, 0, 0, 0.2)",
  };

  // Initial state: Congratulatory message with fake button
  if (!fakeButtonClicked) {
    return (
      <div style={containerStyle}>
        <h1 style={h1Style}>Congratulations!</h1>
        <p style={pStyle}>
          Oh good, you have done this far! You’ve completed all missions with a
          score of {totalScore}. But there’s one more challenge ahead!
        </p>
        <button
          onClick={handleFakeButtonClick}
          style={buttonStyle}
          onMouseOver={(e) => {
            e.currentTarget.style.transform = buttonHoverStyle.transform;
            e.currentTarget.style.boxShadow = buttonHoverStyle.boxShadow;
          }}
          onMouseOut={(e) => {
            e.currentTarget.style.transform = "scale(1)";
            e.currentTarget.style.boxShadow = buttonStyle.boxShadow;
          }}
        >
          Claim Your Prize
        </button>
      </div>
    );
  }
  // After fake button click: Show half photo and riddle
  else if (!challengeSolved) {
    return (
      <div style={containerStyle}>
        <img
          src="/images/mobile_card.jpg"
          alt="Half Mobile Card"
          style={{
            width: "100%",
            maxWidth: "400px",
            borderRadius: "12px",
            boxShadow: "0 4px 12px rgba(0, 0, 0, 0.3)",
            clipPath: "inset(0 0 50% 0)",
            marginBottom: "1.5rem",
          }}
        />
        <p style={pStyle}>
          Oops, that’s only half the mobile card—showing just some of the
          numbers! To see the full prize and all the numbers, solve this riddle:
        </p>
        <p style={{ ...pStyle, fontStyle: "italic", color: "#93c5fd" }}>
          "I am not alive, but I grow; I don’t have lungs, but I need air; I
          don’t have a mouth, but water kills me. What am I?"
        </p>
        <input
          type="text"
          value={answer}
          onChange={handleAnswerChange}
          style={inputStyle}
          placeholder="Enter your answer"
        />
        <button
          onClick={checkAnswer}
          style={buttonStyle}
          onMouseOver={(e) => {
            e.currentTarget.style.transform = buttonHoverStyle.transform;
            e.currentTarget.style.boxShadow = buttonHoverStyle.boxShadow;
          }}
          onMouseOut={(e) => {
            e.currentTarget.style.transform = "scale(1)";
            e.currentTarget.style.boxShadow = buttonStyle.boxShadow;
          }}
        >
          Submit
        </button>
        {feedback && (
          <p style={{ color: "#f87171", marginTop: "1rem" }}>{feedback}</p>
        )}
      </div>
    );
  }
  // After solving challenge: Show full photo
  else {
    return (
      <div style={containerStyle}>
        <h1 style={h1Style}>Congratulations! Here’s your full prize!</h1>
        <img
          src="/images/mobile_card_final.jpg"
          alt="Full Mobile Card"
          style={{
            width: "100%",
            maxWidth: "400px",
            borderRadius: "12px",
            boxShadow: "0 4px 12px rgba(0, 0, 0, 0.3)",
          }}
        />
      </div>
    );
  }
}